<template>
  <div class="mapmode">
    <el-header style="height: 61px">
      <el-menu
          class="el-menu-homepage"
          mode="horizontal"
          background-color="#1F4B62"
          text-color="#fff"
          active-text-color="#ffd04b">
        <el-tooltip class="item" effect="light" content="点击回到主页" placement="bottom">
          <el-menu-item index="10" @click="backtoHomepage()">
            回到主页
          </el-menu-item>
        </el-tooltip>
      </el-menu>
      <div class="searchBox" style="width: 100%;height: 80px;background-color: white;padding: 20px;text-align: left">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="酒店名称">
            <el-input v-model="formInline.hname" placeholder="请输入酒店名称"></el-input>
          </el-form-item>
          <el-form-item label="关键词">
            <el-input v-model="formInline.hotelkeyword" placeholder="请输入关键词，如地标、景点等" style="width: 300px">
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onQuery">查询</el-button>
          </el-form-item>
          <el-form-item>
            <a href="#" style="color: #37a9e5" v-on:click="showNearby()" onclick="return false;">
              附近搜索
            </a>
          </el-form-item>
          <el-tooltip class="item" effect="dark" content="点击重置地图中心" placement="bottom">
            <el-form-item style="clear: both;float: right">
              <el-button type="primary" @click="setCenter">重置</el-button>
            </el-form-item>
          </el-tooltip>
        </el-form>
      </div>
    </el-header>
    <el-main>
      <div class="baidumap" id="allmap">
      </div>
      <div class="resultBox" style="clear: both;width: 25%;float: left;background-color: white;overflow-y: auto">
        <span v-if="this.hotellist.length === 0"><h6 style="font-weight: bold">请输入条件查询目标酒店</h6></span>
        <el-col v-for="item in this.hotellist" :key="item.hotelId">
          <div class="card_wrap">
            <el-card shadow="hover" style="text-align: left;">
              <div class="card_content" style="{
                      border-right: 1px solid #dadfe6;
                      display: inline-flex; width: 100%;
                      flex: 1;
                    }">
                <div class="info" style="flex: 1;text-align: left;">
                  <span><h5 style="font-weight: bold;font-size: 18px">{{item.hname}}</h5></span>
                  <span><h6 style="color: darkgray;font-size: 12px">{{item.addressline1}}</h6></span>
                  <el-rate
                      v-model="item.starRating"
                      :colors="colors"
                      :disabled="true"
                  >
                  </el-rate>
                </div>
                <div class="hotel_link" style="clear: left;flex: 1;float: bottom;text-align: right;">
                  <span><h3 style="color: #f60">¥{{item.ratesFrom}}</h3></span>
                  <span><h5 style="color: #f8c000">{{item.ratingAverage}}分</h5></span>
                  <el-button type="primary" size="medium" class="button" @click="setPointCenter(item.longitude,item.latitude)">查看详情</el-button>
                </div>
              </div>
            </el-card>
          </div>
        </el-col>
      </div>
    </el-main>
  </div>
</template>

<script>
import BaiduMap from 'vue-baidu-map/components/map/Map.vue'
import LoadBaiduMapScript from '@/assets/js/map'
import hotelmarker from '../../static/hotel-marker.png'
export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: "mapmode",
  data() {
    return {
      map: '',
      points: [],
      markers: [],
      infos: [],
      labels: [],
      pageNo: 1,
      pageSize: 147,
      colors: ['#99A9BF', '#F7BA2A', '#ff4c25'],
      pointSingle: [],
      mode_value: 'mapmode',
      hotellist: [{
        'h_id': '1297',
        'rev_review_id': '10001',
        'chainName': 'Wyndham Hotels & Resorts',
        'brandId': '640',
        'brandName': 'Wyndham Hotels and Resorts',
        'hotelName': 'Wyndham Grand Plaza Royale Palace',
        'hotelFormerlyName': '',
        'hname': '成都天之府温德姆至尊豪廷大酒店',
        'addressline1': 'NO.1088 Middle Wangcong Road, West Hi-Tech Zone',
        'addressline2': '',
        'zipcode': '',
        'city': '成都',
        'state': 'Sichuan',
        'country': '中国',
        'countryisocode': 'CN',
        'starRating': 5,
        'longitude': 103.900795,
        'latitude': 30.792871,
        'url': 'https://www.agoda.com/partners/partnersearch.aspx?hid=234485',
        'checkin': '14:00:00',
        'checkout': '12:00:00',
        'numberrooms': '420',
        'numberfloors': '21',
        'yearopened': '2010',
        'yearrenovated': '',
        'photo1': 'http://pix4.agoda.net/hotelimages/234/234485/234485_15061214090029386790.jpg?s=312x',
        'photo2': 'http://pix4.agoda.net/hotelimages/234/234485/234485_15061115590029296571.jpg?s=312x',
        'photo3': 'http://pix1.agoda.net/hotelimages/234/234485/234485_15061214090029386751.jpg?s=312x',
        'photo4': 'http://pix3.agoda.net/hotelimages/234/234485/234485_15061214090029386758.jpg?s=312x',
        'photo5': 'http://pix4.agoda.net/hotelimages/234/234485/234485_15061214090029386761.jpg?s=312x',
        'overview': '成都天之府温德姆至尊豪廷大酒店是一家5星级酒店，可让您的 成都之行变得更完美。 酒店拥有高品质的服务以及完善的设施，可满足游客们的所有需求。 在酒店内，您会发现内设免费房内无线网络, 24小时安保, 每日客房清洁服务, 传真机, 日用品投递服务等设施。 客房装饰精美，部分还内设 平板电视, 无线上网(免费), 禁烟房, 空调, 暖气等。 酒店专门为住客准备了健身中心, 桑拿, 室内游泳池, 水疗中心, 按摩等，可大大提升您对酒店的满意度。 在 成都天之府温德姆至尊豪廷大酒店 住宿期间，你会发现酒店内气氛温馨，服务一流，带给您宾至如归的感受。',
        'ratesFrom': 625,
        'continentId': 2,
        'continentName': 'Asia',
        'cityId': 4729,
        'countryId': 191,
        'numberOfReviews': 20,
        'ratingAverage': 7.2,
        'ratesCurrency': 'CNY'
      }],
      formInline: {
        hname: '',
        hotelkeyword: ''
      },
      searchFlag: false,
      queryId: ''
    }
  },
  created () {
    if (this.$route.params.hotelId === 'null') {
      // console.log('无传参')
    } else {
      // console.log('传参成功' + this.$route.params.hotelId)
      this.queryId = this.$route.params.hotelId
      // console.log(this.queryId)
      this.findSingleHotel()
    }
  },
  async mounted () {
    // await loadBMap()
    await LoadBaiduMapScript()
    this.initMap()
  },
  methods: {
    initMap () {
      // console.log('initMap')
      // eslint-disable-next-line no-undef
      this.map = new BMap.Map('allmap')
      // eslint-disable-next-line no-undef
      var centerPoint = new BMap.Point(104.07231, 30.663467)
      this.map.centerAndZoom(centerPoint, 12)
      this.map.enableScrollWheelZoom(true)
      // eslint-disable-next-line no-undef
      this.map.addControl(new BMap.NavigationControl())
      // eslint-disable-next-line no-undef
      this.map.addControl(new BMap.ScaleControl())
      // eslint-disable-next-line no-undef
      this.map.addControl(new BMap.OverviewMapControl({isOpen: true, anchor: 'BMAP_ANCHOR_BOTTOM_RIGHT'}))
      // eslint-disable-next-line no-undef
      this.map.addControl(new BMap.MapTypeControl())
      window.map = this.map
      var Markers = []
      window.markers = Markers
      var infos = []
      window.infos = infos
      var cpage = 1
      window.cpage = cpage
      // this.showMarker()
    },
    backtoHomepage () {
      this.$router.push('/homepage')
    },
    onQuery () {
      const self = this
      if (this.formInline.hotelname === '' && this.formInline.hotelkeyword === '') {
        this.openNull()
      } else {
        this.$axios({
          url: '/api/cdhotel/getHotelByInfo',
          method: 'GET',
          params: {
            hname: self.formInline.hname,
            info: self.formInline.hotelkeyword
          }
        }).then(
            successResponse => {
              if (successResponse.data.data.length === 0) {
                self.openEmpty()
                self.hotellist = []
                self.clearOverlays()
              } else {
                self.hotellist = successResponse.data.data
                self.showMarker()
              }
            }).catch(err => {
          console.log(err, 'err')
        })
      }
    },
    findSingleHotel () {
      const self = this
      this.$axios({
        url: '/api/cdhotel/getById/' + self.queryId,
        method: 'GET'
      }).then(
          successResponse => {
            self.hotellist = successResponse.data.data
            // console.log(self.hotellist)
          }).catch(err => {
        console.log(err, 'err')
      })
      var timeShow = function () {
        self.showMarker()
      }
      setTimeout(timeShow, 2000)
    },
    setCenter () {
      // eslint-disable-next-line no-undef
      var centerPoint = new BMap.Point(104.10194, 30.65984)
      this.map.centerAndZoom(centerPoint, 12)
    },
    showNearby () {
      // console.log('附近搜索')
      this.setPointCenter(104.10194, 30.65984)
      // eslint-disable-next-line no-undef
      var point = new BMap.Point(104.10194, 30.65984)
      // eslint-disable-next-line no-undef
      var circle = new BMap.Circle(point, 1000, { strokeColor: 'blue', strokeWeight: 2, strokeOpacity: 0.5 })
      this.map.addOverlay(circle)
      this.map.centerAndZoom(point, 16)
    },
    showMarker () {
      this.clearOverlays()
      // console.log('showMarker')
      // eslint-disable-next-line no-undef
      var icon = new BMap.Icon(hotelmarker, new BMap.Size(48, 48))
      for (var i = 0; i < this.hotellist.length; i++) {
        // eslint-disable-next-line no-undef
        var point = new BMap.Point(this.hotellist[i].longitude, this.hotellist[i].latitude)
        this.points.push(point)
        // eslint-disable-next-line no-undef
        var marker = new BMap.Marker(this.points[i], {icon: icon})
        this.markers.push(marker)
        // eslint-disable-next-line no-undef
        var label = new BMap.Label(this.hotellist[i].hotelTranslatedName, {
          // eslint-disable-next-line no-undef
          'offset': new BMap.Size(9, -20)
        })
        label.setStyle({
          display: 'none',
          color: 'black',
          fontSize: '14px',
          height: '20px',
          lineHeight: '20px',
          textAlign: 'center'
        })
        this.markers[i].setLabel(label)
        this.map.addOverlay(this.markers[i])
        this.markers[i].addEventListener('mouseover', function (e) {
          var showlabel = this.getLabel()
          showlabel.setStyle({
            display: 'block'
          })
        })
        this.markers[i].addEventListener('mouseout', function (e) {
          var movelabel = this.getLabel()
          movelabel.setStyle({
            display: 'none'
          })
        })
        var content = "<div style='margin: 0;font-family: 微软雅黑;width: 300px; height: 200px'><b class='iw_poi_title'>" + '酒店详情</b>' +
            "<img style='float:right;margin:auto;flex: 1' id='imgDemo' src='" + this.hotellist[i].photo1 + "' width='100%' height='100%'/>" +
            "<div class='iw_poi_content' style='clear: both;font-size: 26px;float: right;color: red;'>¥" + this.hotellist[i].ratesFrom + '</div>' +
            "<div class='iw_poi_content' style='clear: both;font-size: 18px;float: right;color: #f0ad4e;'>" + this.hotellist[i].ratingAverage + '分</div>' +
            "<div class='iw_poi_content' style='font-weight: bold;font-size: 18px'>" + this.hotellist[i].hotelTranslatedName + '</div>' +
            "<div class='iw_poi_content' style='font-size: 12px;color: gray'>" + this.hotellist[i].hotelName + '</div>' +
            "<div class='iw_poi_content' style='font-size: 14px;color: orangered;font-weight: bold'>" + this.hotellist[i].starRating + '星级</div>' +
            "<div class='iw_poi_content' style='font-size: 14px;'>地址:" + this.hotellist[i].addressline1 + '</div>' +
            '</div></div>'
        // eslint-disable-next-line no-undef
        var info = new BMap.InfoWindow(content)
        this.infos.push(info)
        let _myinfo = this.infos[i]
        this.markers[i].addEventListener('click', function () {
          this.openInfoWindow(_myinfo)
        })
        label.addEventListener('click', function () {
          this.openInfoWindow(_myinfo)
        })
        if (this.markers[i].isOpen) {
          label.hide()
          this.openInfoWindow(_myinfo)
        }
        this.setZoom()
      }
    },
    clearOverlays () {
      // console.log('clear')
      let allOverlayList = this.map.getOverlays()
      for (var i = 0; i < allOverlayList.length; i++) {
        this.map.removeOverlay(allOverlayList[i])
      }
      this.map.clearOverlays()
      this.points = []
      this.markers = []
      this.infos = []
      this.labels = []
    },
    setZoom () {
      var view = this.map.getViewport(this.points)
      var mapZoom = view.zoom
      var centerPoint = view.center
      this.map.centerAndZoom(centerPoint, mapZoom)
    },
    setPointCenter (lon, lat) {
      // eslint-disable-next-line no-undef
      var point = new BMap.Point(lon, lat)
      var points = []
      points.push(point)
      var view = this.map.getViewport(points)
      var mapZoom = view.zoom
      var centerPoint = view.center
      this.map.centerAndZoom(centerPoint, mapZoom)
    },
    openNull () {
      this.$message.error('请输入查询条件')
    },
    openEmpty () {
      this.$message.warning('未查询到信息')
    }
  },
  watch: {
    mode_value () {
      if (this.mode_value === 'listmode') {
        this.$router.push('/search')
      }
    }
  }
}
</script>

<style scoped>
.el-header {
  clear: both;
  position: absolute;
  z-index: 999;
  width: 100%;
  top: 0px;
  left: 0px;
  padding: 0px;
}
.resultBox {
  clear: both;
  position: fixed;
  z-index: 999;
  width: 200px;
  height: calc(100vh - 40px);
  right: 0px;
  top: 140px;
  padding: 0px;
}
.baidumap {
  width: 75%;
  float: left;
  height: calc(90vh - 30px);
  overflow-y: hidden;
  overflow-x: hidden;
}
.iw_poi_title {
  color: #CC5522;
  font-size: 14px;
  font-weight: bold;
  overflow: hidden;
  padding-right: 13px;
  white-space: nowrap
}

.iw_poi_content {
  font: 12px arial, sans-serif;
  overflow: visible;
  padding-top: 4px;
  white-space: -moz-pre-wrap;
  word-wrap: break-word
}
.iw_button {
  font: 13px arial, sans-serif;
  color: #CC5522;
  overflow: visible;
  padding-top: 4px;
  white-space: -moz-pre-wrap;
  word-wrap: break-word
}
</style>
